.auth-container {
  background-color: $secundary;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.auth-container_div {
  background-color: white;
  padding: 30px 20px;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.25);

  form {
    display: flex;
    flex-direction: column;
  }
}

.auth-container_title {
  font-size: 20px;
  font-weight: 100;
}

.auth-container_input {
  font-size: 14px;
  padding: 10px;
  border: none;
  margin: 10px 0 10px;

  &:focus {
    border-bottom: 2px solid $primary;
    outline: none;
  }
}

.auth-container_form {
  width: 100%;
  border: none;
}

.auth-container_div-redirect {
  background-color: $secundary;
  border-radius: 3px;
  width: 80%;
  display: flex;
  justify-content: center;
  padding: 10px;
  margin-top: 20px;
  font-weight: 100;

  p {
    margin-right: 20px;
  }
}

.auth__alert-error {
  border-radius: 5px;
  color: rgb(243, 16, 16);
  display: flex;
  justify-content: flex-start;
  margin-bottom: 10px;
  padding: 0 0 0 10px;
  font-size: 12px;
}
