.auth-btn {
  width: 100%;
  padding: 10px;
  margin: 0;
  background-color: $primary;
  border: 0;
  border-radius: 3px;
  margin: 10px 0 10px;
  font-size: 16px;
  color: white;

  &:hover {
    background-color: darken($color: $primary, $amount: 5);
  }
}

.navbar-btn {
  padding: 5px 10px;
  margin: 0;
  background-color: $primary;
  border: 0;
  border-radius: 3px;
  font-size: 16px;
  color: white;
}

.float-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  bottom: 25px;
  font-size: 25px;
  padding: 25px;
  position: fixed;
  right: 25px;
  cursor: pointer;
  z-index: 10;
}

.btn-float-delete {
  bottom: 25px;
  padding: 10px;
  position: fixed;
  left: 25px;
  cursor: pointer;
  z-index: 10;
}
