// Estos estilos se agregan para que funcione el Calendar
.calendar-container {
  display: flex;
  flex-flow: column;
  height: 100vh;
}

// Esto viene de el componente calendar, de la documentacion de ReactBigCalendar
.rbc-calendar {
  height: 100%;
}

.rbc-toolbar-label {
  color: $primary;
}

.rbc-today {
  background-color: rgb(187, 187, 187);
}

.calendar_navbar-container {
  background-color: $dark-grey;

  display: flex;
  align-items: center;
  justify-content: space-between;

  span {
    color: white;
    margin-left: 15px;
  }

  button {
    margin: 10px 15px 10px 10px;
  }
}
